import { gsap } from "gsap";
import { currentPath } from './config.js';

const boxFinish = document.querySelector(".box-finish");

if (boxFinish) {
    const domainAndPath = currentPath.origin + currentPath.pathname.substring(0, currentPath.pathname.lastIndexOf('/') + 1);
    var audioBoxFinish = new Audio(domainAndPath + "/src/sound/WindyHillPianoRemix.mp3");

    // Hàm tính số ngày giữa hai ngày
    const get_day_of_time = (d1, d2) => {
        let date1 = new Date(d1.getFullYear(), d1.getMonth(), d1.getDate());
        let date2 = new Date(d2.getFullYear(), d2.getMonth(), d2.getDate());
    
        return Math.ceil((date2 - date1) / (24 * 60 * 60 * 1000));
    };

    // Hàm tính số năm và số ngày giữa hai ngày
    const get_years_and_days = (d1, d2) => {
        let totalDays = Math.ceil((d2 - d1) / (24 * 60 * 60 * 1000));

        // Tính số năm từ số ngày
        let years = 0;
        let daysInYear = 365;

        // Tính số năm và số ngày còn lại
        while (true) {
            let nextYear = new Date(d1.getFullYear() + 1, d1.getMonth(), d1.getDate());
            let daysInCurrentYear = Math.ceil((nextYear - d1) / (24 * 60 * 60 * 1000));

            if (totalDays >= daysInCurrentYear) {
                years++;
                totalDays -= daysInCurrentYear;
                d1 = new Date(nextYear.getFullYear(), nextYear.getMonth(), nextYear.getDate());
            } else {
                break;
            }
        }

        // Trả lại số năm và số ngày còn lại
        return { years, days: totalDays };
    };

    // Gán sự kiện click bằng Event Delegation
    document.addEventListener("click", (event) => {
        if (event.target.matches(".box-finish .batnhac")) {
            audioBoxFinish.loop = true;
            audioBoxFinish.play();
        } else if (event.target.matches(".box-finish .tatnhac")) {
            audioBoxFinish.pause();
            audioBoxFinish.currentTime = 0;
        } else if (event.target.matches(".box-finish .display-wedding-invitation")) {
            document.querySelector(".box-finish .wedding-invitation-box")?.classList.toggle("active");
        } else if (event.target.closest(".display-resultKyNiem-paper")) {
            document.querySelector(".box-achievement-paper .notice")?.classList.toggle("active");
        } else if (event.target.closest(".display-resultKyNiem-cotton")) {
            document.querySelector(".box-achievement-cotton .notice")?.classList.toggle("active");
        }
    });

    // Danh sach ky niem
    const anniversaries = [
        { name: "Yêu nhau", date: "February 18, 2024 19:41:52 GMT+0700", id: "timeDaYeuNhau_show" },
        { name: "Kết hôn", date: "April 02, 2024 11:00:00 GMT+0700", id: "timeDaKetHon_show" },
        { name: "Kỷ niệm 1 năm", date: "April 02, 2025 11:00:00 GMT+0700", id: "resultKyNiemDamCuoiGiay_show", box: ".box-achievement-paper" },
        { name: "Kỷ niệm 2 năm", date: "April 02, 2026 11:00:00 GMT+0700", id: "resultKyNiemDamCuoiBong_show", box: ".box-achievement-cotton" }
    ];
    
    // Hàm cập nhật số năm và ngày
    const updateAnniversary = (startDate, elementId) => {
        let today = new Date();
        let result = get_years_and_days(new Date(startDate), today);
        let element = document.getElementById(elementId);
        if (element) {
            element.innerHTML = `${result.years} năm ${result.days} ngày`;
        }
    };
    
    // Hàm cập nhật kỷ niệm (dùng cho các cột mốc tương lai)
    const updateFutureAnniversary = (date, elementId, boxClass) => {
        let today = new Date();
        let daysLeft = get_day_of_time(today, new Date(date));
        let element = document.getElementById(elementId);
        if (element) {
            element.innerHTML = `${daysLeft} ngày`;
        }
        if (boxClass) {
            document.querySelector(`${boxClass} .${daysLeft > 0 ? "future" : "now"}`).classList.add("active");
        }
    };
    
    // Chạy vòng lặp cập nhật tất cả các kỷ niệm
    anniversaries.forEach(ann => {
        if (ann.box) {
            updateFutureAnniversary(ann.date, ann.id, ann.box);
        } else {
            updateAnniversary(ann.date, ann.id);
        }
    });


    gsap.to(".box-finish .gs-animation-1", {
        opacity: 1,
        duration: 5,
        x: 0,
        y: 0,
        startAt: {
            opacity: 0,
            x: -300,
            y: -300
        }
    });

    gsap.to(".box-finish .gs-animation-2", {
        opacity: 1,
        duration: 5,
        x: 0,
        y: 0,
        startAt: {
            opacity: 0,
            x: 300,
            y: 300
        }
    });

    gsap.to(".box-finish .gs-animation-3", {
        opacity: 1,
        duration: 1,
        y: 0,
        startAt: {
            opacity: 0,
            y: -100
        }
    });

    gsap.to(".box-finish .gs-animation-4", {
        opacity: 1,
        duration: 1,
        x: 0,
        startAt: {
            opacity: 0,
            x: -100
        }
    });

    gsap.to(".box-finish .gs-animation-5", {
        opacity: 1,
        duration: 1,
        x: 0,
        startAt: {
            opacity: 0,
            x: 100
        }
    });

    gsap.to(".box-finish .gs-animation-6", {
        rotation: 360 * 10,
        opacity: 1,
        duration: 5,
        startAt: {
            opacity: 0,
        }
    });

    gsap.to(".box-finish .gs-animation-7", {
        opacity: 1,
        duration: 1,
        y: 0,
        startAt: {
            opacity: 0,
            y: 100
        }
    });
}