import Swiper from 'swiper';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import { Navigation, Pagination, Autoplay } from 'swiper/modules';

export function initSwiper() {
    new Swiper(".slider-achievement", {
        modules: [Autoplay], // Bổ sung modules
        loop: true,
        autoplay: {
            delay: 0,
            disableOnInteraction: false,
        },
        speed: 5000,
        slidesPerView: 2,
        spaceBetween: 8,
        // Responsive theo từng kích thước màn hình
        breakpoints: {
            480: {
                slidesPerView: 2, // Điện thoại nhỏ
                spaceBetween: 8,
            },
            768: {
                slidesPerView: 4, // Tablet
                spaceBetween: 10,
            },
            1024: {
                slidesPerView: 8, // Laptop tiêu chuẩn
                spaceBetween: 12,
            },
            1366: {
                slidesPerView: 12, // Màn hình lớn hơn
                spaceBetween: 15,
            },
            1600: {
                slidesPerView: 16, // Màn hình rất lớn
                spaceBetween: 18,
            },
            1920: {
                slidesPerView: 20, // Màn hình cực lớn (Full HD)
                spaceBetween: 20,
            },
        },
    });
}
